/*------------------------------------*    #BODY
\*------------------------------------*/
/**
 * Body base styles
 */
body {
  background: #fff;
  color: #808080;
  font: 100%/1.5 "HelveticaNeue", "Helvetica", "Arial", sans-serif;
  -webkit-text-size-adjust: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/*------------------------------------*    #ANIMATION
\*------------------------------------*/
/**
 * Fade Animation
 */
.animate-fade {
  -webkit-transition: opacity 0.3s ease-out;
  -moz-transition: opacity 0.3s ease-out;
  -ms-transition: opacity 0.3s ease-out;
  -o-transition: opacity 0.3s ease-out;
  transition: opacity 0.3s ease-out;
}

.animate-fade:hover {
  opacity: 0;
}

/*------------------------------------*    #FORMS
\*------------------------------------*/
/**
 * Fieldset base styles
 */
fieldset {
  border: 0;
  padding: 0;
  margin: 0;
}

/**
 * Label base styles
 */
label {
  display: block;
  padding-bottom: 0.2rem;
  font-weight: bold;
}

/**
 * Form button, input, select, and text area base styles
 * 1) Zero out margin and inherit font styles
 */
button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}

/**
 * Input and text area base styles
 * 1) Width entire container, gray border, padding
 */
input, textarea {
  width: 100%;
  border: 1px solid #a5a5a5;
  border-radius: 6px;
  padding: 1rem;
}

/**
 * Input areas base styles
 * 1) Get rid of webkit appearance of these areas
 */
input[type=text], input[type=search], input[type=url], input[type=number], textarea {
  -webkit-appearance: none;
}

/**
 * Button/Submit button base styles
 */
button, input[type="submit"] {
  padding: 0.5em;
  background: #444444;
  border: 1px solid #808080;
  cursor: pointer;
}

/**
 * Checkboxes/Radio button base styles
 */
input[type="checkbox"],
input[type="radio"] {
  width: auto;
  margin-right: 0.3em;
}

/**
 * Search cancel button and search decoration webkit appearance none
 */
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/*------------------------------------*    #GLOBAL CLASSES
\*------------------------------------*/
/**
 * Clearfix
 */
.cf {
  *zoom: 1;
}

/**
 * Clearfix pseudo-elements
 */
.cf:before, .cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */
}

.cf:after {
  clear: both;
}

/**
 * Completely remove from the flow and screen readers.
 */
.is-hidden {
  display: none !important;
  visibility: hidden !important;
}

/**
 * Completely remove from the flow but leave available to screen readers.
 */
.is-vishidden {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

/**
 * Text align right
 */
.align-right {
  text-align: right;
}

/**
 * Text align center
 */
.align-center {
  text-align: center;
}

/**
 * Text Align left
 */
.align-left {
  text-align: left;
}

/**
 * Hide on small screens
 */
@media all and (max-width: 46.8em) {
  .hide-small {
    display: none;
  }
}

/**
 * Hide on medium screens
 */
@media all and (min-width: 46.8em) and (max-width: 50em) {
  .hide-med {
    display: none;
  }
}

/**
 * Hide on large screens
 */
@media all and (min-width: 50em) {
  .hide-large {
    display: none;
  }
}

/**
 * Show valid text color
 */
.valid {
  color: #03804d;
}

/**
 * Show error text color
 */
.error {
  color: #b12a0b;
}

/**
 * Show warning text color
 */
.warning {
  color: #a59b15;
}

/**
 * Show informational text color
 */
.information {
  color: #0192d0;
}

/**
 * Display secondary font family
 */
.font-secondary {
  font-family: Georgia, Times, "Times New Roman", serif;
}

/*------------------------------------*    #HEADINGS
\*------------------------------------*/
/**
 * Heading 1
 */
h1 {
  font-size: 3rem;
  line-height: 1.2;
}

/**
 * Heading 2
 */
h2 {
  line-height: 1.2;
}

/**
 * Heading 3
 */
h3 {
  line-height: 1.2;
}

/**
 * Heading 4
 */
/**
 * Heading 5
 */
/**
 * Heading 6
 */
/**
 * Subheading
 */
.subheading {
  font-family: Georgia, Times, "Times New Roman", serif;
  font-weight: normal;
}

/*------------------------------------*    #LINKS
\*------------------------------------*/
/**
 * Links
 */
a {
  color: #808080;
  text-decoration: none;
  outline: 0;
  transition: all 0.2s ease;
}

a:hover, a:focus {
  color: #444444;
}

/*------------------------------------*    #LINKS
\*------------------------------------*/
/**
 * Ordered/Unordered list base styles
 */
ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

/**
 * Definition list base styles
 */
dl {
  overflow: hidden;
  margin: 0 0 1em;
}

/**
 * Terms/Names base styles
 */
dt {
  font-weight: bold;
}

/**
 * Definition list description base styles
 */
dd {
  margin-left: 0;
}

/*------------------------------------*    #MAIN
\*------------------------------------*/
/**
 * Main content area
 */
[role=main] {
  flex: 1;
}

/*------------------------------------*    #MEDIA ELEMENTS
\*------------------------------------*/
/**
 * Make media elements responsive
 */
img, video, object {
  max-width: 100%;
  height: auto;
}

/**
 * Iframe base styles
 */
iframe {
  margin-bottom: 1em;
}

/**
 * Figure base styles
 */
figure {
  margin-bottom: 1em;
  /**
	 * Figure image base styles
	 */
}

figure img {
  margin-bottom: 0.5em;
}

/**
 * Figure caption base styles
 */
figcaption {
  font-style: italic;
}

/*------------------------------------*    #RESET
\*------------------------------------*/
/**
 * Use border box on all elements
 */
* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/**
 * Zero out margin and padding on most elements as base style
 */
html, body, div, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, ol, ul, li, form, legend, label, table, header, footer, nav, section, figure {
  margin: 0;
  padding: 0;
}

/**
 * Display block as base for these elements
 */
header, footer, nav, section, article, hgroup, figure {
  display: block;
}

/*------------------------------------*    #TABLE
\*------------------------------------*/
/**
 * Table base styles
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid #808080;
  width: 100%;
}

/**
 * Table header base styles
 */
th {
  text-align: left;
  border: 1px solid #808080;
  padding: 0.2em;
}

/**
 * Table cell base styles
 */
td {
  border: 1px solid #808080;
  padding: 0.2em;
}

/*------------------------------------*    #TEXT
\*------------------------------------*/
/**
 * Paragraph base styles
 */
p {
  margin-bottom: 1em;
}

/**
 * Blockquote base styles
 */
blockquote {
  font-style: italic;
  border-left: 1px solid #808080;
  color: #808080;
  padding-left: 1em;
  margin-bottom: 1em;
}

/**
 * Horizontal rule base styles
 */
hr {
  border: 0;
  height: 2px;
  background: #d9d9d9;
  margin: 2em 0;
}

/**
 * Abbreviation base styles
 */
abbr {
  border-bottom: 1px dotted #808080;
  cursor: help;
}

/*------------------------------------*    #BLOCKS
\*------------------------------------*/
/**
 * 1) Blocks are collections of text and/or images/media working together as a unit.
 */
/*------------------------------------*	#HERO BLOCK
\*------------------------------------*/
.c-block-hero {
  display: block;
  position: relative;
}

/**
 * Hero Image
 * 1) This is the primary hero image that runs full-bleed across the layout
 */
.c-block-hero__img {
  display: block;
  width: 100%;
  max-height: 50vh;
  object-fit: cover;
}

/**
 * Hero Headline
 * 1) This sits overtop of the hero image when space permits
 */
.c-block-hero__headline {
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  padding: 1rem;
  font-size: 2.6rem;
  transition: all 0.2s ease;
}

@media all and (min-width: 50em) {
  .c-block-hero__headline {
    position: absolute;
    top: 3rem;
    left: 1rem;
    width: 50%;
    border-radius: 6px;
  }
}

.c-block-hero:hover .c-block-hero__headline, .c-block-hero:focus .c-block-hero__headline {
  background: #444444;
}

/*------------------------------------*	#MEDIA BLOCK
\*------------------------------------*/
/**
 * 1) Media block contains an image (or some other media) on the left
 * and text on the right.
 * 2) For more info, read http://www.stubbornella.org/content/2010/06/25/the-media-object-saves-hundreds-of-lines-of-code/
 */
.c-block-media {
  display: flex;
  transition: all 0.2s ease;
}

.c-block-media:hover .c-block-media__headline, .c-block-media:focus .c-block-media__headline {
  color: #5C832F;
}

.c-block-media--small {
  font-size: 0.875rem;
}

/**
 * Media Container
 * 1) This is the container that holds the block image (or media)
 */
.c-block-media__media {
  width: 20%;
  max-width: 11em;
  margin-right: 1rem;
}

.c-block-media--small .c-block-media__media {
  max-width: 3.5em;
  margin-right: 0.5rem;
}

@media all and (min-width: 46.8em) {
  .c-block-media__media {
    width: 50%;
    margin-right: 1.5rem;
  }
}

.c-block-media__img {
  border-radius: 6px;
}

.c-block-media__headline {
  margin-bottom: 0.2em;
  color: #595959;
  transition: all 0.2s ease;
}

.c-block-media--small .c-block-media__headline {
  font-size: 1rem;
}

.c-block-media__body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
}

.c-block-media__excerpt {
  color: #a5a5a5;
  transition: all 0.2s ease;
}

.c-block-media--small .c-block-media__excerpt {
  margin-bottom: 0;
  line-height: 1.2;
}

/*------------------------------------*	#TOUT BLOCK
\*------------------------------------*/
/**
 * 1) The tout block contains an image and headline overlayed over the image.
 */
.c-block-tout {
  display: inline-block;
  margin-bottom: 0.5em;
  position: relative;
  overflow: hidden;
  border-radius: 6px;
}

/**
 *  Tout image
 */
.c-block-tout__img {
  display: block;
}

/**
 *  Tout headline
 */
.c-block-tout__headline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  padding: 0.5em;
  font-size: 1.4rem;
  transition: all 0.2s ease;
}

.c-block-tout:hover .c-block-tout__headline, .c-block-tout:focus .c-block-tout__headline {
  padding-bottom: 2rem;
}

/*------------------------------------*	#TILE BLOCK
\*------------------------------------*/
/**
 * 1) The tile block contains a span and headline overlayed on a background
 */
.c-block-tile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  border-radius: 6px;
  padding: 1em 1em;
  text-align: center;
  overflow: hidden;
  transition: all 0.2s ease;
}

.c-block-tile:hover, .c-block-tile:focus {
  background: rgba(0, 0, 0, 0.7);
  color: #fff;
}

@media all and (min-width: 55rem) {
  .c-block-tile {
    padding: 4em 1em;
  }
}

/**
 * Featured tile block
 */
.c-block-tile--featured {
  min-height: 200px;
}

/**
 * Tile block value
 */
.c-block-tile__value {
  font-size: 1.5rem;
  line-height: 1.2;
  /**
	 * Tile value when inside featured tile parent selector
	 */
}

.c-block-tile--featured .c-block-tile__value {
  font-size: 2.5rem;
}

@media all and (min-width: 46.8em) {
  .c-block-tile--featured .c-block-tile__value {
    font-size: 5rem;
  }
}

@media all and (min-width: 55rem) {
  .c-block-tile__value {
    font-size: 3rem;
  }
}

.c-block-tile__label {
  color: #d9d9d9;
  font-weight: normal;
  font-size: 1rem;
}

/*------------------------------------*    #BUTTONS
\*------------------------------------*/
/**
 * Button
 */
.c-btn {
  display: inline-block;
  background: #3b4a20;
  color: #fff;
  line-height: 1;
  font-weight: bold;
  padding: 1rem 2rem;
  border: 0;
  border-radius: 6px;
  text-align: center;
  transition: background 0.2s ease;
  /**
	 * Disabled button
	 */
}

.c-btn:hover, .c-btn:focus {
  background: #5C832F;
  color: #fff;
}

.c-btn.disabled {
  background: #d9d9d9;
  color: #808080;
}

/**
 * Small button
 */
.c-btn-small {
  padding: 0.5em;
}

/**
 * Large button
 */
.c-btn-large {
  padding: 0.5em;
  text-transform: uppercase;
  background: #808080;
  font-size: 1.4rem;
  font-weight: normal;
}

/**
 * Text button
 */
.c-text-btn {
  position: relative;
  background: none;
  border: none;
  font-style: normal;
  font-weight: bold;
  font-size: 1.2rem;
}

.c-text-btn:after {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  background: url("../../../images/icon-arrowhead.svg") no-repeat;
  top: 7px;
  right: -26px;
  transition: all 0.2s ease;
}

.c-text-btn:hover, .c-text-btn:focus {
  color: #444444;
}

.c-text-btn:hover:after, .c-text-btn:focus:after {
  right: -28px;
}

.c-block-media--small .c-text-btn {
  font-size: 1rem;
}

/**
 * Button on overlay
 */
.c-btn--overlay {
  background: #a5a5a5;
}

/*------------------------------------*    #CAROUSEL
\*------------------------------------*/
/**
 * Horizontal carousel
 */
.carousel-horizontal {
  margin-bottom: 1em;
  overflow: hidden;
  position: relative;
}

/**
 * Carousel island container
 */
.carousel-island-container {
  overflow: hidden;
}

/**
 * Carousel container
 */
.carousel-container {
  position: relative;
}

/**
 * Carousel list
 */
/**
 * Carousel controls
 */
.carousel-controls {
  display: table;
  width: 100%;
  margin: -0.5em 0 1em;
  /**
	 * Carousel pagination
	 */
  /**
	 * Carousel next button
	 */
}

.carousel-controls a, .carousel-controls div {
  display: table-cell;
  padding: 0.5em;
}

.carousel-controls .carousel-pagination {
  text-align: center;
}

.carousel-controls .carousel-next {
  text-align: right;
}

/*------------------------------------*    #FOOTER
\*------------------------------------*/
/**
 * Footer
 */
.c-footer {
  clear: both;
  overflow: hidden;
  background: #3b4a20;
  color: #fff;
  line-height: 1.2;
  padding: 1em 0;
}

/**
 * Footer nav
 */
.c-footer__info {
  max-width: 40em;
}

.c-footer__info a {
  color: #d9d9d9;
  text-decoration: underline;
}

.c-footer__info a:hover, .c-footer__info a:focus {
  text-decoration: none;
}

/*------------------------------------*    #FOOTER NAVIGATION
\*------------------------------------*/
/**
 * Footer Nav
 */
.c-footer-nav {
  display: block;
  margin-right: 1rem;
}

/**
 * Footer Nav List
 */
.c-footer-nav__list {
  display: flex;
  flex-direction: column;
}

@media all and (min-width: 24em) {
  .c-footer-nav__list {
    flex-direction: row;
  }
}

/**
 * Footer Nav List Item
 */
.c-footer-nav__item {
  width: 50%;
}

@media all and (min-width: 24em) {
  .c-footer-nav__item {
    width: inherit;
  }
}

/**
 * Footer Nav Links
 */
.c-footer-nav__link {
  font-weight: bold;
  display: block;
  padding: 1rem 2rem 1rem 0;
  color: #fff;
}

.c-footer-nav__link:hover, .c-footer-nav__link:focus {
  color: #a5a5a5;
}

/*------------------------------------*    #FORMS
\*------------------------------------*/
/**
 * Search form
 */
.c-search-form {
  display: none;
}

.c-search-form--is-active {
  display: block;
  position: absolute;
  top: 67px;
  right: 2px;
  width: 100%;
  padding: 1em;
  box-shadow: 0px 3px 5px -2px rgba(0, 0, 0, 0.2);
  background: #fff;
  z-index: 1;
}

@media all and (min-width: 29.75em) {
  .c-search-form {
    display: block;
  }
}

/**
 * Search input
 */
.c-search-form__input {
  padding: 0.5rem;
}

/**
 * Stacked form button wrapper
 */
.c-form__actions {
  margin-bottom: 2rem;
}

/*------------------------------------*    #FIELDS
\*------------------------------------*/
.c-field {
  margin-bottom: 1rem;
}

.c-field__note {
  font-size: 0.75rem;
}

.c-field__note a {
  text-decoration: underline;
}

/*------------------------------------*    #HEADER
\*------------------------------------*/
/**
 * Header
 */
.c-header {
  z-index: 8;
  display: flex;
  align-items: center;
  padding: 0.5em 0;
  box-shadow: 0 3px 5px -2px rgba(0, 0, 0, 0.45);
}

@media all and (min-height: 46.8em) {
  .c-header {
    padding: 1em 0;
  }
}

/**
 * Header Controls
 * 1) This container holds the primary nav and search menu
 */
.c-header__controls {
  margin-left: auto;
  display: flex;
  align-items: center;
  padding-right: 1rem;
}

/**
 * Nav toggle button
 * 1) Styles for the general nav toggle button
 */
.nav-toggle {
  display: block;
  padding: 0.9rem 1rem 0.7rem;
  font-size: 1.3rem;
  line-height: 1;
}

@media all and (min-width: 46.8em) {
  .nav-toggle {
    display: none;
  }
}

@media all and (max-width: 17em) {
  .nav-toggle {
    padding-left: 0.2rem;
    padding-right: 0.2rem;
  }
}

/**
 * Search nav toggle button
 * 1) Specific nav toggle button for the search form
 */
.nav-toggle-search {
  display: block;
}

@media all and (min-width: 29.75em) {
  .nav-toggle-search {
    display: none;
  }
}

/*------------------------------------*    #LOGO
\*------------------------------------*/
.c-logo {
  display: block;
  max-width: 200px;
}

.c-box__header .c-logo {
  max-width: 12em;
  margin: 0 auto;
}

.c-logo-link {
  display: inline-block;
  padding: 0.2rem 1rem;
}

/*------------------------------------*    #ICONS
\*------------------------------------*/
/**
 * Icon fonts
 */
@font-face {
  font-family: 'icons';
  src: url("../fonts/icons.eot");
  src: url("../fonts/icons.eot?#iefix") format("embedded-opentype"), url("../fonts/icons.woff") format("woff"), url("../fonts/icons.ttf") format("truetype"), url("../fonts/icons.svg#icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

/**
 * Use the following CSS code if you want to use data attributes for inserting your icons
 */
[data-icon]:before {
  font-family: 'icons';
  content: attr(data-icon);
  speak: none;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
}

/**
 * Icons and icon pseudo-elements
 */
.icon-twitter:before, .icon-stumbleupon:before, .icon-pinterest:before, .icon-linkedin:before, .icon-google-plus:before, .icon-search:before, .icon-play:before, .icon-menu:before, .icon-arrow-left:before, .icon-arrow-right:before, .icon-bubble:before, .icon-facebook:before, .icon-feed:before, .icon-youtube:before, .icon-tag:before, .icon-tumblr:before, .icon-instagram, .icon-podcast, .icon-apple, .icon-android, .icon-arrow:after, .icon-envelope:before {
  font-family: 'icons';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
