/*------------------------------------*    #LISTS
\*------------------------------------*/
/**
 * Inline list
 */
.inline-list li {
  display: inline-block;
}

/**
 * Social list
 */
.social-list li {
  margin: 0 0.4rem 1em 0;
}

.social-list a {
  font-size: 1.6em;
}

/**
 * Headline list
 */
.headline-list {
  margin-bottom: 1em;
}

.headline-list.flush {
  margin: 0;
}

.headline-list h4 {
  font-weight: normal;
}

.headline-list li {
  padding: 0.25em 0;
  border-top: 1px solid #a5a5a5;
}

/**
 * Post list
 */
.post-list li {
  margin-bottom: 1em;
}

/**
 * Bullet list
 */
.bullet-list {
  list-style: square;
  margin: 0 0 1em 1.2em;
  line-height: 1.3;
}

.bullet-list li {
  margin-bottom: 1em;
}

/**
 * Text list
 */
.text-list {
  margin: 0 0 1em;
  line-height: 1.3;
}

.text-list li {
  margin-bottom: 1em;
}

/**
 * Media List
 */
.c-media-list__item {
  margin-bottom: 1.5em;
}

/**
 * Tile list
 */
.c-tile-list {
  display: flex;
  flex-direction: column;
}

@media all and (min-width: 55rem) {
  .c-tile-list {
    flex-direction: row;
    flex-wrap: wrap;
  }
}

/**
 * Tile list item
 */
.c-tile-list__item {
  width: 100%;
  margin-bottom: 1em;
  position: relative;
}

.c-tile-list__item:nth-child(2n) {
  padding-right: 0;
}

@media all and (min-width: 55rem) {
  .c-tile-list__item {
    width: 50%;
    margin: 0;
    padding: 0 1em 1em 0;
  }
}

/*------------------------------------*    #MESSAGING
\*------------------------------------*/
/**
 * Alert
 */
.alert {
  text-align: center;
  padding: 0 1rem;
  background: #f7f9f9;
  overflow: hidden;
  font-size: 1em;
  animation: slideDown 2s forwards;
}

.alert a {
  color: inherit;
  text-decoration: underline;
}

/**
 * Error alert
 */
.alert--error {
  color: #b12a0b;
  background: #fdded8;
}

/**
 * Success alert
 */
.alert--success {
  color: #03804d;
  background: #d4f3e6;
}

/**
 * Warning alert
 */
.alert--warning {
  color: #a59b15;
  background: #fffecf;
}

/**
 * Info alert
 */
.alert--info {
  color: #0192d0;
  background: #d3f2ff;
}

/**
 * Slidedown animation
 */
@keyframes slideDown {
  0% {
    max-height: 0;
    padding: 0;
  }
  100% {
    padding: 1rem;
    max-height: 12em;
  }
}

/**
 * Overlay
 */
.c-overlay {
  display: block;
  padding: 1em;
  background: rgba(0, 0, 0, 0.7);
  border-radius: 6px;
  position: relative;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  border-radius: 6px;
  text-align: center;
  padding: 1em;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.c-overlay--is-hidden {
  display: none;
}

.c-tile-grid__primary .c-overlay {
  position: absolute;
}

.c-tile-list__item .c-overlay {
  position: absolute;
}

.c-section .c-overlay {
  position: absolute;
}

/**
 * Overlay Intro
 */
.c-overlay__intro {
  font-size: 1.5rem;
  color: #fff;
}

/*------------------------------------*    #PRIMARY NAVIGATION
\*------------------------------------*/
/**
 * Navigation tag
 */
.c-primary-nav {
  display: none;
}

.c-primary-nav--is-active {
  display: block;
  position: absolute;
  padding: 1em 2em;
  top: 67px;
  right: 0;
  box-shadow: -3px 3px 5px -2px rgba(0, 0, 0, 0.2);
  z-index: 2;
  background: #fff;
}

@media all and (min-width: 46.8em) {
  .c-primary-nav {
    display: block;
    margin-right: 1rem;
  }
}

/**
 * Nav List
 */
.c-primary-nav__list {
  display: none;
}

@media all and (min-width: 46.8em) {
  .c-primary-nav__list {
    display: flex;
  }
}

.c-primary-nav--is-active .c-primary-nav__list {
  display: block;
}

/**
 * Nav Links
 */
.c-primary-nav__link {
  font-weight: bold;
  display: block;
  padding: 1rem;
}

/**
 * Pagination
 */
.pagination {
  overflow: hidden;
}

.pagination li {
  float: left;
  border-right: 1px solid #d9d9d9;
}

.pagination li:last-child {
  border: 0;
}

.pagination a {
  padding: 1em;
}

/*------------------------------------*    #PAGE HEADER
\*------------------------------------*/
/**
 * 1) The page header contains the page's heading, as perhaps other page information
 *    such as description or actions (edit, delete, etc).
 */
.c-page-header__title {
  padding: 1rem 0 0.5rem;
  position: relative;
  color: #444444;
}

.c-page-header__title:after {
  content: "";
  display: block;
  position: absolute;
  width: 3rem;
  height: 0.5rem;
  bottom: 0;
  background: #a5a5a5;
}

/*------------------------------------*    #SECTIONS
\*------------------------------------*/
/**
 * Section
 */
.c-section {
  position: relative;
  margin: 0 0 2em;
}

@media all and (min-width: 39.8em) {
  .l-sidebar .c-section {
    margin-right: 2em;
    width: 50%;
  }
  .l-sidebar .c-section:last-child {
    margin-right: 0;
  }
}

@media all and (min-width: 50em) {
  .l-sidebar .c-section {
    width: 100%;
    margin-right: 0;
  }
}

/**
 * Section title
 */
.c-section__title {
  position: relative;
  color: #444444;
  margin-bottom: 1.5rem;
}

.c-section__title:after {
  content: "";
  display: block;
  position: absolute;
  width: 2rem;
  height: 0.3rem;
  top: 30px;
  left: 1px;
  background: #a5a5a5;
}

/**
 * Section block
 */
.c-tile-grid {
  background: url(../../images/sample/hero-mountains.jpg) no-repeat 50% 50% #444444;
  background-size: cover;
}

.c-tile-grid__inner {
  display: flex;
  flex-direction: column;
}

@media all and (min-width: 46.8em) {
  .c-tile-grid__inner {
    flex-direction: row;
  }
}

.c-tile-grid__primary {
  display: flex;
  position: relative;
  width: 100%;
  margin-bottom: 1rem;
}

@media all and (min-width: 46.8em) {
  .c-tile-grid__primary {
    width: 50%;
  }
}

@media all and (min-width: 55rem) {
  .c-tile-grid__primary {
    width: 30%;
  }
}

.c-tile-grid__secondary {
  flex: 1;
}

@media all and (min-width: 46.8em) {
  .c-tile-grid__secondary {
    padding: 0 0 0 1em;
  }
}

/*------------------------------------*    #TEXT
\*------------------------------------*/
/**
 * Passages of text
 */
.text a {
  text-decoration: underline;
}

.text ul {
  list-style: disc;
  margin: 0 0 1em 1.2em;
}

.text ul ul {
  margin-bottom: 0;
}

.text ol {
  list-style: decimal;
  margin: 0 0 1em 1.5em;
}

.text ol ol {
  margin-bottom: 0;
}

/**
 * Text passage
 */
.s-text-passage {
  margin-bottom: 1rem;
}

/**
 * Image within text passage
 */
.c-text-passage__img {
  border-radius: 6px;
  margin-bottom: 1rem;
}

/*------------------------------------*    #BOX
\*------------------------------------*/
/**
 * 1) This component is a modal-like
 */
.c-box {
  border: 1px solid #808080;
  border-radius: 6px;
  padding: 2rem;
  margin: 2rem;
  align-self: center;
  justify-content: center;
  max-width: 32em;
  margin: 2rem auto;
}

.c-box__header {
  text-align: center;
  margin-bottom: 2rem;
}

/*------------------------------------*    #FEEDBACK
\*------------------------------------*/
/**
 * 1) Feedback consists of components that give information back to the user. Examples include progress bars, progress wheels, etc.
 */
/**
 * Progress bar
 */
.c-progress {
  -webkit-appearance: none;
  appearance: none;
  border: 1px solid rgba(0, 0, 0, 0.5);
  background: #cdcaa2;
  background-size: auto;
  width: 100%;
  max-width: 300px;
  height: 0.5rem;
  border-radius: 6px;
  margin-bottom: 1em;
}

.c-progress::-moz-progress-bar {
  background-color: red;
  background-image: linear-gradient(to right, red, #f06d06, yellow, green);
  background-size: 300px 50px;
  border-radius: 6px;
}

.c-progress::-webkit-progress-value {
  background-color: red;
  background-image: linear-gradient(to right, red, #f06d06, yellow, green);
  background-size: 300px 50px;
  border-radius: 6px;
}

progress[value]::-webkit-progress-bar {
  border-radius: 6px;
}

.anim::-moz-progress-bar {
  -moz-transition: width 4s ease;
}

.anim::-webkit-progress-value {
  -webkit-transition: width 4s ease;
  -o-transition: width 4s ease;
  transition: width 4s ease;
}

.c-progress.start::-moz-progress-bar {
  width: 0% !important;
  background: #cdcaa2 !important;
}

.c-progress.start::-webkit-progress-value {
  width: 0% !important;
  background: #cdcaa2 !important;
}

/*------------------------------------*    #LAYOUT
\*------------------------------------*/
/**
 * Layout container
 */
.l {
  max-width: 80em;
  margin: 0 auto;
  padding: 1rem;
}

/*------------------------------------*    #TEMPLATES
\*------------------------------------*/
/**
 * 2 column layout
 */
.l--two-col {
  display: flex;
  flex-direction: column;
  /**
	 * Sidebar
	 */
}

@media all and (min-width: 50em) {
  .l--two-col .l-main {
    width: 70%;
    padding-right: 1em;
  }
}

@media all and (min-width: 39.8em) {
  .l--two-col .l-sidebar {
    display: flex;
  }
}

@media all and (min-width: 50em) {
  .l--two-col .l-sidebar {
    flex-direction: column;
    width: 30%;
    padding: 0 0 0 1em;
  }
}

@media all and (min-width: 50em) {
  .l--two-col {
    flex-direction: row;
  }
}

/*------------------------------------*    #GRIDS
\*------------------------------------*/
/**
 * Grid
 */
.g {
  overflow: hidden;
  margin: 0 -0.5em;
}

/**
 * Grid item
 */
.gi {
  padding: 0.5em;
}

.gi img {
  display: block;
}

@media all and (min-width: 46.8em) {
  .gi {
    float: left;
  }
}

/**
 * 2 up grid
 */
@media all and (min-width: 46.8em) {
  .g-2up > .gi {
    float: left;
    width: 50%;
  }
  .g-2up > .gi:nth-of-type(odd) {
    clear: left;
  }
}

/**
 * Grid side by side
 */
.g-half > .gi {
  float: left;
  width: 50%;
}

.g-half > .gi:nth-of-type(odd) {
  clear: left;
}

/**
 * 3 up grid
 */
@media all and (min-width: 46.8em) {
  .g-3up > .gi {
    float: left;
    width: 50%;
  }
  .g-3up > .gi:nth-of-type(2n+1) {
    clear: left;
  }
}

@media all and (min-width: 50em) {
  .g-3up > .gi {
    width: 33.3333333%;
  }
  .g-3up > .gi:nth-of-type(2n+1) {
    clear: none;
  }
  .g-3up > .gi:nth-of-type(3n+1) {
    clear: left;
  }
}

/**
 * 4 up grid
 */
@media all and (min-width: 46.8em) {
  .g-4up > .gi {
    float: left;
    width: 50%;
  }
  .g-4up > .gi:nth-of-type(2n+1) {
    clear: both;
  }
}

@media all and (min-width: 50em) {
  .g-4up > .gi {
    width: 25%;
  }
  .g-4up > .gi:nth-of-type(2n+1) {
    clear: none;
  }
  .g-4up > .gi:nth-of-type(4n+1) {
    clear: left;
  }
}

/**
 * Quarter grid always displayed side by side
 */
.g-quarter > .gi {
  float: left;
  width: 24%;
}

.g-quarter > .gi:nth-of-type(4n+1) {
  clear: left;
}

/**
 * Grid max 4 up
 */
@media all and (min-width: 29.75em) {
  .g-max4 > .gi {
    float: left;
    width: 50%;
  }
  .g-max4 > .gi:nth-of-type(2n+1) {
    clear: both;
  }
}

@media all and (min-width: 39.8em) {
  .g-max4 > .gi {
    width: 33.3333333%;
  }
  .g-max4 > .gi:nth-of-type(2n+1) {
    clear: none;
  }
  .g-max4 > .gi:nth-of-type(3n+1) {
    clear: left;
  }
}

@media all and (min-width: 50em) {
  .g-max4 > .gi {
    width: 25%;
  }
  .g-max4 > .gi:nth-of-type(3n+1) {
    clear: none;
  }
  .g-max4 > .gi:nth-of-type(4n+1) {
    clear: left;
  }
}

/**
 * Grid max of 5 up
 */
.g-max5 > .gi {
  float: left;
  width: 50%;
}

.g-max5 > .gi:nth-of-type(2n+1) {
  clear: both;
}

@media all and (min-width: 39.8em) {
  .g-max5 > .gi {
    width: 33.3333333%;
  }
  .g-max5 > .gi:nth-of-type(2n+1) {
    clear: none;
  }
  .g-max5 > .gi:nth-of-type(3n+1) {
    clear: left;
  }
}

@media all and (min-width: 46.8em) {
  .g-max5 > .gi {
    width: 25%;
  }
  .g-max5 > .gi:nth-of-type(3n+1) {
    clear: none;
  }
  .g-max5 > .gi:nth-of-type(4n+1) {
    clear: left;
  }
}

@media all and (min-width: 48em) {
  .g-max5 > .gi {
    width: 20%;
  }
  .g-max5 > .gi:nth-of-type(4n+1) {
    clear: none;
  }
  .g-max5 > .gi:nth-of-type(5n+1) {
    clear: left;
  }
}
